@use "../utils" as *;

.container {
    padding: var(--space-lg) var(--space-md);

    @include breakpoint(sm) {
        padding: var(--space-lg) var(--space-xl);
    }
}

.container-inline {
    padding-inline: var(--space-xl);
}

.container-block {
    padding-block: var(--space-lg);
}
